import React from "react";
import {
  MdArrowForward,
  MdDelete,
  MdEdit,
  MdRemoveRedEye,
} from "react-icons/md";
import { SiAddthis } from "react-icons/si";
import Table from "../../components/Table/Table";
import { AccessTaskInterface } from "../../shared/interfaces";
interface AppProps {
  handleSubmit: (e: React.FormEvent<HTMLFormElement>, step: 1 | 2) => void;
  roleTitle: string;
  setAccessToRole: (value: AccessTaskInterface[]) => void;
  accessToRole: AccessTaskInterface[];
  error: {
    element: string;
    msg: string;
  };
  setError: (value: { element: string; msg: string }) => void;
  loadingRequest: boolean;
}

const StepTwo: React.FC<AppProps> = (props) => {
  const updateFieldChanged = (
    index: number,
    target: "create" | "view" | "update" | "delete",
    value: boolean
  ) => {
    let newArr: AccessTaskInterface[] = props.accessToRole; // copying the old datas array
    switch (target) {
      case "create":
        newArr[index].permitted.create = value;
        break;
      case "view":
        newArr[index].permitted.view = value;
        break;
      case "update":
        newArr[index].permitted.update = value;
        break;
      default:
        newArr[index].permitted.delete = value;
        break;
    }

    props.setAccessToRole(newArr);
  };
  const formTasks = (): JSX.Element[] => {
    return props.accessToRole.map((access, index) => (
      <tr
        key={access.id + index}
        className="accordion border-b border-grey-light hover:bg-gray-100"
      >
        <td className="text-gray-800 text-base font-medium text-left px-3">
          {access.task}
        </td>
        <td className="py-1 px-3">
          <input
            type="checkbox"
            className="form-checkbox h-5 w-5 text-gray-600"
            onChange={() => {
              updateFieldChanged(index, "create", !access.permitted.create);
              props.setError({ element: "", msg: "" });
            }}
            checked={access.permitted.create}
          />
        </td>
        <td className="py-1 px-3">
          <input
            type="checkbox"
            className="form-checkbox h-5 w-5 text-gray-600"
            onChange={() => {
              updateFieldChanged(index, "view", !access.permitted.view);
              props.setError({ element: "", msg: "" });
            }}
            checked={access.permitted.view}
          />
        </td>
        <td className="py-1 px-3">
          <input
            type="checkbox"
            className="form-checkbox h-5 w-5 text-gray-600"
            onChange={() => {
              updateFieldChanged(index, "update", !access.permitted.update);
              props.setError({ element: "", msg: "" });
            }}
            checked={access.permitted.update}
          />
        </td>
        <td className="py-1 px-3">
          <input
            type="checkbox"
            className="form-checkbox h-5 w-5 text-gray-600"
            onChange={() => {
              updateFieldChanged(index, "delete", !access.permitted.delete);
              props.setError({ element: "", msg: "" });
            }}
            checked={access.permitted.delete}
          />
        </td>
      </tr>
    ));
  };
  return (
    <div>
      <form
        className="max-w-xl mx-auto"
        onSubmit={(e) => props.handleSubmit(e, 2)}
      >
        <div className="mb-5">
          <div className="mb-5 mt-3">
            <h1 className="text-3xl font-semibold text-center">
              Access Permissions Settings
            </h1>
            <div className="flex items-center gap-2 mt-1 justify-center">
              <MdArrowForward className="w-6 h-6" />
              <h1 className="text-xl font-semibold text-gray-600 text-center">
                {props.roleTitle}
              </h1>
            </div>
          </div>
          <div className="md:flex mb-3">
            <div className="grid grid-cols-2 grid-rows-2 gap-4">
              <div className="inline-flex gap-2 items-center">
                <SiAddthis className="w-12 h-12 text-gray-600" />
                <div className="flex flex-col">
                  <h3 className="font-semibold text-xl">Create</h3>
                  <span className="text-gray-600 text-sm md:pr-1">
                    This permission gives the user the ability to create
                  </span>
                </div>
              </div>

              <div className="inline-flex gap-2 items-center">
                <MdRemoveRedEye className="w-12 h-12 text-gray-600" />
                <div className="flex flex-col">
                  <h3 className="font-semibold text-xl">View</h3>
                  <span className="text-gray-600 text-sm md:pr-1">
                    This permission gives the user the ability to view
                  </span>
                </div>
              </div>

              <div className="inline-flex gap-2 items-center">
                <MdEdit className="w-12 h-12 text-gray-600" />
                <div className="flex flex-col">
                  <h3 className="font-semibold text-xl">Update</h3>
                  <span className="text-gray-600 text-sm md:pr-1">
                    This permission gives the user the ability to update
                  </span>
                </div>
              </div>

              <div className="inline-flex gap-2 items-center">
                <MdDelete className="w-12 h-12 text-gray-600" />
                <div className="flex flex-col">
                  <h3 className="font-semibold text-xl">Delete</h3>
                  <span className="text-gray-600 text-sm md:pr-1">
                    This permission gives the user the ability to delete
                  </span>
                </div>
              </div>
            </div>
          </div>

          <p
            className={`text-red-700 mt-1 ml-1 text-base italic ${
              props.error.element !== "role-access" && "hidden"
            }`}
          >
            {props.error.msg}
          </p>

          <Table
            columns={[
              {
                columnTitle: "Access Name",
                alignText: "left",
                padding: "p-3",
              },
              { columnTitle: "Create", alignText: "left" },
              { columnTitle: "View", alignText: "left" },
              { columnTitle: "Update", alignText: "left" },
              { columnTitle: "Delete", alignText: "left" },
            ]}
            rows={formTasks}
            pagination={false}
            search={true}
            thColor="gray"
          />

          <div className="flex justify-end gap-5 mt-4">
            <button
              className="btn bg-primary text-white hover:bg-blue-600 focus:outline-none"
              type="submit"
              disabled={props.loadingRequest}
            >
              {props.loadingRequest === true ? "Saving..." : "Complete"}
            </button>
          </div>
        </div>
      </form>
    </div>
  );
};

export default StepTwo;
