import React, { useEffect, useState } from "react";
import { BiSort } from "react-icons/bi";
import { MdNavigateBefore, MdNavigateNext, MdSearch } from "react-icons/md";

const FC_makeID = (length: number = 5): string => {
  let result: string = "";
  let characters: string =
    "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
  let charactersLength = characters.length;
  for (let i: number = 0; i < length; i++) {
    result += characters.charAt(Math.floor(Math.random() * charactersLength));
  }
  return result;
};

export const sortThisColumn = (
  tableId: string,
  colIndex: number = 0,
  dir: "asc" | "desc" = "asc"
) => {
  var table,
    rows,
    switching,
    i,
    x,
    y,
    shouldSwitch,
    switchcount = 0;
  table = document.getElementById(tableId) as HTMLTableElement;
  switching = true;
  /* Make a loop that will continue until
  no switching has been done: */
  if (table) {
    while (switching) {
      //start by saying: no switching is done:
      switching = false;
      rows = table.rows;
      /*Loop through all table rows (except the
    first, which contains table headers):*/
      for (i = 1; i < rows.length - 1; i++) {
        //start by saying there should be no switching:
        shouldSwitch = false;
        /*Get the two elements you want to compare,
      one from current row and one from the next:*/
        x = rows[i].getElementsByTagName("TD")[colIndex];
        y = rows[i + 1].getElementsByTagName("TD")[colIndex];
        /*check if the two rows should switch place,
      based on the direction, asc or desc:*/
        if (dir === "asc" && x && y) {
          if (x.innerHTML.toLowerCase() > y.innerHTML.toLowerCase()) {
            //if so, mark as a switch and break the loop:
            shouldSwitch = true;
            break;
          }
        } else if (dir === "desc" && x && y) {
          if (x.innerHTML.toLowerCase() < y.innerHTML.toLowerCase()) {
            //if so, mark as a switch and break the loop:
            shouldSwitch = true;
            break;
          }
        }
      }
      if (shouldSwitch && rows[i].parentNode) {
        /*If a switch has been marked, make the switch
      and mark that a switch has been done:*/
        rows[i].parentNode!.insertBefore(rows[i + 1], rows[i]);
        switching = true;
        //Each time a switch is done, increase this count by 1:
        switchcount++;
      } else {
        /*If no switching has been done AND the direction is "asc",
      set the direction to "desc" and run the while loop again.*/
        if (switchcount === 0 && dir === "asc") {
          dir = "desc";
          switching = true;
        }
      }
      // if (i === 0) continue;
      // rows.cells[i].style.backgroundColor = "blue";
    }
  }
};

export interface TableColumnsInterface {
  columnTitle: JSX.Element | string;
  alignText: string;
  padding?: string;
  isSortAllowed?: boolean;
}

interface TableProps {
  columns: TableColumnsInterface[];
  rows: Function;
  hideColumns?: boolean;
  pagination?: boolean;
  search?: boolean;
  tableSize?: string;
  tableClass?: string;
  tableId?: string;
  rounded?: boolean;
  searchBG?: string;
  thColor?: string;
  loading?: boolean;
}

const Table: React.FC<TableProps> = (props) => {
  const startPage = 1;
  const tableId =
    !props.tableId || props.tableId === "myTable" ? FC_makeID() : props.tableId;
  let table: HTMLElement, tr: HTMLCollectionOf<HTMLTableRowElement>;
  const [totalItems, setTotalItems] = useState<number>(0);
  const [totalPages, setTotalPages] = useState<number>(1);
  const [currentPage, setCurrentPage] = useState<number>(1);
  const [pages, setPages] = useState<number[]>();
  const [pageSize, setPageSize] = useState<number>(10);
  const [startIndex, setStartIndex] = useState<number>(0);
  const [endIndex, setEndIndex] = useState<number>(0);

  useEffect(() => {
    props.pagination && tablePagination();
  });

  const filterTable = (
    data: string,
    filterType?: "default" | "field"
  ): void => {
    var filter, tr, td, rows: number, cols: number, txtValue;
    if (filterType === "field" && data === "all") data = "";
    filter = data.toLowerCase();
    table = document.getElementById(tableId!)!;
    tr = table.getElementsByTagName("tr");
    for (rows = 0; rows < tr.length; rows++) {
      for (cols = 0; cols < props.columns.length; cols++) {
        td = tr[rows].getElementsByTagName("td")[cols];
        if (td) {
          txtValue = td.textContent || td.innerText;
          if (txtValue.toLowerCase().indexOf(filter) > -1) {
            tr[rows].style.display = "";
            break;
          } else {
            tr[rows].style.display = "none";
          }
        }
      }
    }
  };

  const tablePagination = (): void => {
    table = document.getElementById(tableId)!;
    tr = table.getElementsByTagName("tr");
    setTotalItems(tr.length);
    setTotalPages(Math.ceil(totalItems / pageSize));

    // calculate start and end item indexes on the current page
    setStartIndex((currentPage - 1) * pageSize);
    setEndIndex(Math.min(startIndex + pageSize - 1, totalItems - 1));

    /**
     * TODO!
     * There is a problem with this condition
     */
    let _pages = Array.from(Array(totalPages + 1 - startPage).keys()).map(
      (i) => startPage + i
    );
    if (!pages || _pages.length !== pages.length) {
      setPages(_pages);
    }

    for (let i: number = 0; i < tr.length; i++) {
      if (i >= startIndex && i <= endIndex) {
        tr[i].style.display = "";
      } else {
        tr[i].style.display = "none";
      }
    }
  };

  return (
    <div className={`${props.tableSize} overflow-x-auto`}>
      {/* search box  */}
      {props.search && (
        <div className={`${props.searchBG} w-full mb-2`}>
          {props.search && (
            <div className="bg-white rounded flex items-center w-full p-2 border-2 border-gray-500">
              <MdSearch className="h-5 w-5 fill-current text-gray-400" />
              <input
                type="search"
                placeholder="search"
                onChange={(e) => filterTable(e.target.value)}
                className="w-full h-6 pl-4 text-sm outline-none focus:outline-none bg-white"
                disabled={props.loading}
              />

              {props.pagination && (
                <div className="select">
                  <select
                    className="text-base outline-none focus:outline-none bg-white"
                    onChange={(e) => setPageSize(Number(e.target.value))}
                    disabled={props.loading}
                  >
                    <option value="10">10</option>
                    <option value="25">25</option>
                    <option value="50">50</option>
                    <option value="100">100</option>
                  </select>
                </div>
              )}
            </div>
          )}
        </div>
      )}
      <div className="overflow-x-auto w-full">
        <table
          className={
            props.tableClass
              ? props.tableClass
              : `w-full shadow ${
                  props.rounded && "sm:rounded-lg"
                } table-auto order-table`
          }
          id={tableId}
        >
          {!props.hideColumns && (
            <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-base">
              <tr
                className={`text-left tracking-wide bg-${props.thColor}-100 border-b-2 border-${props.thColor}-500 text-${props.thColor}-600`}
              >
                {props.columns.map((column, index) => (
                  <th
                    key={index}
                    className={`${column.padding} text-${
                      column.alignText
                    } px-3 py-2 ${column.isSortAllowed && "cursor-pointer"}`}
                    onClick={() =>
                      column.isSortAllowed && sortThisColumn(tableId, index)
                    }
                  >
                    <div className="flex items-center gap-1">
                      {column.columnTitle}
                      {column.isSortAllowed && <BiSort />}
                    </div>
                  </th>
                ))}
              </tr>
            </thead>
          )}
          <tbody id="table-data" className="bg-white text-black text-base">
            {props.rows()}
          </tbody>
        </table>
        {props.pagination && (
          <div className="sm:flex-1 sm:flex sm:items-center sm:justify-between mt-4 work-sans">
            <div>
              <span className="text-xs xs:text-sm text-gray-900">
                Showing 1 to {pageSize} of {totalItems - 1} Entries
              </span>
            </div>
            <div>
              <nav className="inline-flex shadow-sm">
                <div>
                  <button
                    type="button"
                    className={`inline-flex items-center px-2 py-2 rounded-l-md border border-gray-300 text-sm font-medium text-gray-500 ${
                      currentPage !== 1 && "bg-white"
                    }`}
                    aria-label="Previous"
                    disabled={currentPage === 1 || props.loading}
                    onClick={() => setCurrentPage(currentPage - 1)}
                  >
                    <MdNavigateBefore className="h-5 w-5" />
                  </button>
                </div>
                <div>
                  {pages &&
                    pages.map((page, pageIndex) => (
                      <button
                        key={pageIndex}
                        className={`-ml-px inline-flex items-center px-4 py-2 border border-gray-300 text-sm font-medium text-gray-700 ${
                          page !== currentPage
                            ? "bg-white"
                            : "bg-gray-100 cursor-default"
                        }`}
                        disabled={page === currentPage || props.loading}
                        onClick={() => setCurrentPage(page)}
                      >
                        {page}
                      </button>
                    ))}
                </div>
                <div>
                  <button
                    type="button"
                    className={`inline-flex items-center px-2 py-2 rounded-r-md border border-gray-300 text-sm leading-5 font-medium text-gray-500 ${
                      currentPage !== totalPages && "bg-white"
                    }`}
                    aria-label="Next"
                    disabled={currentPage === totalPages || props.loading}
                    onClick={() => setCurrentPage(currentPage + 1)}
                  >
                    <MdNavigateNext className="h-5 w-5" />
                  </button>
                </div>
              </nav>
            </div>
          </div>
        )}
      </div>
    </div>
  );
};

export default Table;
