import React, { Fragment, useEffect, useState } from "react";
import { MdClose, MdErrorOutline, MdLock } from "react-icons/md";
import { connect } from "react-redux";
import { StoreState } from "../../reducers";
import AccessRole from "./AccessRole";
import { fetchAccessRoles } from "../../actions/access";
import { AccessType } from "../../reducers/access";
import {
  MainItem,
  SideNavigation,
} from "../../components/Navigation/SideNavigation";
import { toTwoDigit } from "../../shared/dataTest";

enum TabTypes {
  All_Roles = "All Roles",
  Active_Roles = "Active Roles",
  Blocked_Roles = "Blocked Roles",
}

interface AppProps {
  access: AccessType;
  fetchAccessRoles: Function;
}

const Index: React.FC<AppProps> = (props) => {
  const [labelCounts, setLabelCounts] = useState<{
    allRole: number;
    activeRole: number;
    blockedRole: number;
  }>({
    allRole: 0,
    activeRole: 0,
    blockedRole: 0,
  });
  const [roleStatus, setRoleStatus] = useState<TabTypes>(TabTypes.All_Roles);
  const [feedBack, setFeedBack] = useState<{
    type: "success" | "danger" | "warning" | "";
    message: string;
  }>();
  const [loading, setLoading] = useState<boolean>(true);

  useEffect(() => {
    if (!props.access.accessRoles.length && loading) {
      console.log(props.access.accessRoles);
      props.fetchAccessRoles();
    }

    if (props.access.accessRoles.length) {
      setLabelCounts({
        allRole: props.access.accessRoles.length,
        activeRole: props.access.accessRoles.filter((role) => role.status)
          .length,
        blockedRole: props.access.accessRoles.filter((role) => !role.status)
          .length,
      });
      setLoading(false);
    }
  }, [loading, props, props.access.accessRoles]);

  const handleTabs = (): JSX.Element => {
    if (roleStatus === TabTypes.Blocked_Roles) {
      return <AccessRole filterStatus={false} />;
    } else if (roleStatus === TabTypes.Active_Roles) {
      return <AccessRole filterStatus={true} />;
    } else return <AccessRole filterStatus="all" />;
  };

  if (loading === true) {
    return (
      <div className="text-center mt-7 font-bold text-4xl text-gray-400 animate-pulse">
        Loading roles...
      </div>
    );
  }

  return (
    <Fragment>
      <SideNavigation>
        <span className="text-sm mt-2">Filter by Status</span>
        <section
          className="mt-1"
          onClick={() => setRoleStatus(TabTypes.All_Roles)}
        >
          <MainItem
            title={"All Role"}
            count={labelCounts.allRole}
            active={roleStatus === TabTypes.All_Roles ? true : false}
          />
        </section>
        <section
          className="mt-1"
          onClick={() => setRoleStatus(TabTypes.Active_Roles)}
        >
          <MainItem
            title={"Active Role"}
            count={labelCounts.activeRole}
            active={roleStatus === TabTypes.Active_Roles ? true : false}
          />
        </section>
        <section
          className="mt-1"
          onClick={() => setRoleStatus(TabTypes.Blocked_Roles)}
        >
          <MainItem
            title={"Disabled Role"}
            count={labelCounts.blockedRole}
            active={roleStatus === TabTypes.Blocked_Roles ? true : false}
          />
        </section>
      </SideNavigation>
      {/* Title */}
      <div className="w-full my-5 px-1">
        {/* header title */}
        <div className="flex flex-row items-center">
          <MdLock className="text-blue-700 w-28 h-28" />
          <div className="flex flex-col">
            <h1 className="text-3xl font-semibold">System Roles Management</h1>
            <div className="flex flex-row items-center mx-2 md:gap-3 gap-2">
              <div className="flex flex-col mt-1">
                <span className="text-gray-600 text-base">All Role(s)</span>
                <span className="text-gray-600 text-3xl font-bold">
                  {toTwoDigit(labelCounts.allRole)}
                </span>
              </div>
              {roleStatus !== TabTypes.Blocked_Roles && (
                <div className="flex flex-col mt-1">
                  <span className="text-gray-600 text-base">
                    Active Role(s)
                  </span>
                  <span className="text-gray-600 text-3xl font-bold">
                    {toTwoDigit(labelCounts.activeRole)}
                  </span>
                </div>
              )}
              {roleStatus !== TabTypes.Active_Roles && (
                <div className="flex flex-col mt-1">
                  <span className="text-gray-600 text-base">
                    Blocked Role(s)
                  </span>
                  <span className="text-gray-600 text-3xl font-bold">
                    {toTwoDigit(labelCounts.blockedRole)}
                  </span>
                </div>
              )}
            </div>
          </div>
        </div>
        {/* end header title */}
      </div>
      {/* Feedback */}
      {feedBack && feedBack.type !== "" && (
        <div
          className={`alert-${feedBack.type} flex flex-row items-center relative my-2`}
        >
          <span className="absolute top-0 bottom-0 right-0 px-4 py-3">
            <MdClose
              className="h-5 w-5 text-gray-700 hover:font-bold"
              title="Close"
              onClick={() => setFeedBack({ type: "", message: "" })}
            />
          </span>
          <MdErrorOutline className="h-10 w-10" />
          <div className="alert-content ml-4">
            <div className="alert-title font-semibold text-lg">
              {feedBack.type === "success" ? "Success" : "Error"}
            </div>
            <div className="alert-description text-sm">{feedBack.message}</div>
          </div>
        </div>
      )}
      <div className="p-2 overflow-x-auto">{handleTabs()}</div>
    </Fragment>
  );
};

const mapStateToProps = ({ access }: StoreState): { access: AccessType } => {
  return { access: access };
};

export const AccessManagement = connect(mapStateToProps, {
  fetchAccessRoles,
})(Index);
